.home {
  text-align: center;
  font-family: 'Comfortaa', cursive;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 95%;
  width: 100%;
}
.home-name {
  /* color: rgb(0, 111, 238); */
  color: #fff;
  /* font-family: 'Shadows Into Light', cursive; */
  font-family: 'Courier New', Courier, monospace;
  font-size: 3vw;
  /* text-shadow: -8px 4px rgb(255, 218, 98); */
  border: none;
  margin-bottom: 30px;
  white-space: nowrap;
  overflow: hidden;
  /* width: 100%; */
  border-right: 5px solid #2371e6;
  -webkit-animation: type 2s forwards steps(19, end);
          animation: type 2s forwards steps(19, end);
}

/* https://stackoverflow.com/questions/33486228/how-to-fade-in-reveal-one-letter-at-a-time */
@-webkit-keyframes type {
  0% {
    width: 0;
  }

  99% {
    border-right: 5px solid #2371e6;
  }
  100% {
    width: 100%;
    border-right: none;
  }
}
@keyframes type {
  0% {
    width: 0;
  }

  99% {
    border-right: 5px solid #2371e6;
  }
  100% {
    width: 100%;
    border-right: none;
  }
}
.home-statement {
  color: #fff;
  font-size: 4vw;
  display: -ms-flexbox;
  display: flex;
  font-family: 'Courier New', Courier, monospace;
  line-height: 4.5vw;
  margin: 20px 30px;
}

.statement-title {
  /* background: #ededed; */
  font-size: 3.5vw;
  background: -webkit-gradient(linear, left top, left bottom, from(#d9eaf3), color-stop(8%, #fff)) 0 4px;
  background: -webkit-linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background: -o-linear-gradient(top, #d9eaf3 0%, #fff 8%) 0 4px;
  background: linear-gradient(180deg, #d9eaf3 0%, #fff 8%) 0 4px;
  background-size: 100% 23px;
  font-family: 'Shadows Into Light', cursive;
  color: rgba(255, 0, 0, 0.8);
  padding: 0px 5px;
  margin: 10px;
}
.statement-creativity {
  text-shadow: 0px 0px 0px #1e88e5, -3px 2px #1e88e5, 0px 4px rgb(240, 240, 120),
    4px 3px 0px salmon, -5px 4px lightgreen;
}
.statement-happy {
  -webkit-animation: change-colors 25s infinite alternate ease-in-out;
          animation: change-colors 25s infinite alternate ease-in-out;
}
.statement-users {
  -webkit-filter: invert(1);
          filter: invert(1);
}

@-webkit-keyframes change-colors {
  0% {
    color: rgb(0, 111, 238);
  }
  25% {
    color: salmon;
  }
  50% {
    color: lightgreen;
  }
  65% {
    color: #ffea28;
  }
  80% {
    color: #ba68c8;
  }
  100% {
    color: rgb(0, 111, 238);
  }
}

@keyframes change-colors {
  0% {
    color: rgb(0, 111, 238);
  }
  25% {
    color: salmon;
  }
  50% {
    color: lightgreen;
  }
  65% {
    color: #ffea28;
  }
  80% {
    color: #ba68c8;
  }
  100% {
    color: rgb(0, 111, 238);
  }
}



